<script>
import {mapState} from 'vuex'
 
export default {
  name: 'BuscadorProductores',
  data() {
    return {
      // header: productor.usuario.header,
      // logo: productor.usuario.logo,
      horeca: {},
      productos:{},

      cat: '',
      categoria: '',
      families: [],
      familias: [],
      
      dismissSecs: 5,
      dismissCountDown: 0,
      mensaje: {
        color: '',
        texto: ''
      }
    }
  },

  computed: {
    ...mapState(['token', 'id']),
  },
    methods: {
      getUser(){
      let config = {
        headers: {
          token: this.token ,
        }
      }
      this.axios.get(`/horeca/`, config )
        .then(res => {
          this.horeca = res.data.horeca;
          console.log(this.horeca)
        })
        .catch(e => {
          console.log(e)
        })
    },
    trimedString(str){
      if (str.length <= 229) return str;
      return str.substr(0, str.lastIndexOf(' ', 230));
    },
    familiaproductos(cat) {
      let config = {
        headers: {
          token: this.token ,
        }
      }
      let id = this.cat;
      this.axios.get(`/categoria/${id}` , config)
      .then(res => {
        console.log(this.categoria)
        this.categoria = res.data.categoria.nombre;
        this.families = res.data.categoria.familias;
      })
      .catch(e => {
        console.log(e)
      })
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    }
  },
  created () {
    this.getUser();
  },
  watch: {
    cat: {
      handler(cat) {
        this.cat = cat
        this.familiaproductos(cat);
      }
    }
  }
}
</script>

